.App {
  width: 100%;
  height: 100%;
  background-color: black;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 2px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

body{
  color: white;
  background-color: black;
}

img{
  width: 150px;
  height: 150px;
}

p{
  font-size: 20px;
}

#price{
  color: orange;
}

#productBox{
  transition: 0.5s;
  display: inline-block;
  padding: 20px;
  margin: 20px;
  border: 2px solid white;
  border-radius: 33px;
}
#productBox:hover{
  transition: 0.5s;
  cursor: pointer;
  background-color: orange;
}
#productBox:hover p{
  transition: 0.5s;
  color: black; 
}

